@font-face {
    font-family: Inter;
    src: url(Inter/Inter-VariableFont_slnt\,wght.ttf);
    font-display: swap;
}

html, body{
    margin: 0px;
    height: 100%;
    font-family: Inter;
    scroll-behavior: smooth;
}

h1, h2, h3, p{
    margin: 0px;
}

*{
    box-sizing: border-box;
}

a{
    color: #0679d4;
}

#root{
    height: 100%;
    width: 100%;
    display:block;
}

div{
    display: flex;
}

#header{
    position: absolute;
    top: 0px;
    width: 100%;
    height: 65px;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 25px;
    justify-content: space-between;
    color:#b0b4b4;
    z-index: -1;
}

#header-links{
    display: none;
}

/* Mobile menu */

#menu{
   width: 100%;
   height: 100%;
   position: fixed;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   transition: all 0.7s ease; 
   background-color: #010101;
   background-image: url(blurry1.webp);
   background-position: center;
   background-size: contain;
   background-repeat: no-repeat;
   z-index: 1;
}

#hamburger{
    width: 20px;
    height: 15px;
    flex-direction: column;
    position: fixed;
    top: 30px;
    right: 30px;
    z-index: 2;
    mix-blend-mode: difference;
}

span{
    width: 20px;
    height: 5px;
    background-color: #b0b4b4;
    margin-top: 2px;
    border-radius: 3px;
}

.link{
    text-decoration: none;
    color: #b0b4b4;
    font-weight: bolder;
    font-size: 24px;
    margin-top: 27px;
    margin-bottom: 0px;
    transition: opacity 1s ease-in;
}

/*
#menu.open, .link.open{
    visibility: visible;
    opacity: 100%;
}
*/

#menu.open{
    width: 100%;
    height: 100%;
    border-radius: 0px;
}

/*
#menu.closed, .link.closed{
    visibility: hidden;
}
*/

#menu.closed{
    width: 0px;
    height: 0px;
    border-radius: 0px 50px 100px 0px;
}

/*transitioning on visibility doesn't work*/
.link.closed{
    opacity: 0%;
    transition: opacity 0.2s ease-in;
}


/*Hero Section*/
#content{
    min-height: 100%;
    min-width: 100%;
    position: absolute;
    flex-direction: column;
    top: 0px;
    z-index: -2;
}

#page-one{
    min-width: 100%;
    min-height: 100vh;
    flex-direction: column;
    padding-left: 30px;
    background-color: #010101;
    background-image: url(blurry1.webp);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    justify-content: center;
}

#redefine{
    color: #fcbcf5;
    font-weight: bolder;
}

#subtext1{
    color: #b0b4b4;
    margin-top: 10px;
}

/*About Page*/
#about{
    min-width: 100%;
    padding-right: 30px;
    padding-left: 30px;
    padding-top: 40px;
    padding-bottom: 40px;
    flex-direction: column;
    background-color: #f5f5f5;
}

.about-section{
    flex-direction: column;
    margin-top: 10px;
}

h2.about-section{
    letter-spacing: 3px;
}

h3.about-section{
    color: #333333;
    margin-bottom: 10px;
    text-decoration: underline;
    font-weight: bold;
}

p{
    color: #696969;
    font-weight: normal;
}

/*skills page*/
#skills{
    flex-direction: column;
    width: 100%;
    padding: 30px;
    padding-top: 40px;
    padding-bottom: 40px;
    background-image: url(blurry2.webp);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #4b3849;
}

.skills{
    letter-spacing: 3px;
    font-weight: bolder;
}

#skills-content{
    flex-direction: column;
    margin-top: 10px;
}

.field{
    width: 100%;
    margin-top: 8px;
}

.field>h3{
    color: #775875;
}

.field>progress{
    appearance: none;
    width: 40%;
    height: 8px;
    margin-left: auto;
    align-self: center;
    border: none;
    margin-right: 5px;
    border-radius: 5px;
}

.field>progress::-webkit-progress-bar{
    background-color: #ffffff75;
    border-radius: 5px;
}

.field>progress::-moz-progress-bar{
    background-color: #775875;
    border-radius: 5px;
}

.field>progress::-webkit-progress-value{
    background-color: #775875;
    border-radius: 5px;
}

/*contact*/
#contact{
    flex-direction: column;
    padding: 30px;
    padding-top: 40px;
    padding-bottom: 40px;
    color:#333333;
    align-items: center;
}

#contact>h1{
    letter-spacing: 3px;
}

#contacts-block{
    margin-top: 30px;
    flex-direction: column;
    align-items: center;
}

#contacts-block>*{
    margin-top: 10px;
}

footer{
    background-color: #010101;
    background-image: url(blurry1.webp);
    background-position: center;
    background-size: 80%;
    background-repeat: no-repeat;
    padding: 30px;
    color:#b0b4b4;
    font-size: small;
    text-align: center;
}

@media screen and (min-width: 541px) {
    #hamburger{
        display: none;
    }

    #header{
        padding-top: 40px;
        padding-left: 40px;
        padding-right: 40px;
        z-index: 5;
        mix-blend-mode: difference;
        font-size: 24px;
        font-weight: bold;
    }

    #header-links{
        display: flex;
        position: fixed;
        top: 43px;
        right: 40px;
        font-size: 20px;
    }

    #header-links>a{
        text-decoration: none;
        color: #b0b4b4;
        margin-left: 20px;
        
    }

    #header-links>a:hover{
        color: #eeeeee;
        text-decoration: underline;
    }

    #page-one{
        background-size: 70%;
        padding-left: 40px;
    }

    #redefine{
        font-size: 54px;
    }

    #subtext1{
        font-size: 20px;
    }

    #about{
        padding-left: 40px;
        
        padding-top: 70px;
        padding-bottom: 70px;
    }

    .about-section{
        margin-top: 20px;
    }

    h2.about-section{
        font-size: 28px;
    }

    h3{
        font-size: 22px;
    }

    p{
        font-size: 20px;
    }

    #skills{
        padding: 40px;
        padding-top: 50px;
        padding-bottom: 60px;
    }

    h2.skills{
        font-size: 28px;
    }

    #skills-content{
        margin-top: 20px;
    }

    #contact{
        padding: 40px;
        padding-top: 50px;
        padding-bottom: 60px;
        height: 80vh;
        justify-content: center;
        background-color: #f5f5f5;
    }

    #contact>h1{
        font-size: 44px;
    }

    #contacts-block>a{
        font-size: 18px;
    }

    #contacts-block>p{
        font-size: 18px;
    }

    footer{
        font-size: 18px;
    }
}